import React, { PropTypes } from 'react'
import Link from 'gatsby-link'
import thumbs from '../data/thumbs'
import content from '../style/content'

const TechWork = () => (
  <div>
    <h2 {...content.header} id="jump-nav-1">
      Engineering
    </h2>
    <section {...content.thumbs}>
      {thumbs.map(item => (
        <Link to={item.slug} key={item.title} {...content.link}>
          <figure {...content.thumb} {...item.img}>
            <figcaption>
              <p>{item.title}</p>
              <span>- {item.subTitle} -</span>
            </figcaption>
          </figure>
        </Link>
      ))}
    </section>
  </div>
)

PropTypes.TechWork = {}

export default TechWork



