import React, { PropTypes } from 'react'
import Link from 'gatsby-link'
import thumbsDesign from '../data/thumbsDesign'
import content from '../style/content'

const DesignWork = () => (
  <div>
    <h2 {...content.header} id="jump-nav-2">
      Design+
    </h2>
    <section {...content.thumbs}>
      {thumbsDesign.map(item => (
        <Link to={item.slug} key={item.title} {...content.link}>
          <figure {...content.thumb} {...item.img}>
            <figcaption>
              <p>{item.title}</p>
              <span>- {item.subTitle} -</span>
            </figcaption>
          </figure>
        </Link>
      ))}
    </section>
  </div>
)

PropTypes.DesignWork = {}

export default DesignWork



