import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Footer from '../components/Footer'
import Navigation from '../components/Nav'
import DesignWork from '../components/DesignWork'
import TechWork from '../components/TechWork'
import content from '../style/content'
import header from '../style/header'
import smoothScroll from 'smoothscroll'

class IndexPage extends Component {
  constructor(props) {
    super(props)
    this.state = {
      navOpen: false,
    }
  }

  handleClick() {
    const { navOpen } = this.state
    this.setState({ navOpen: !navOpen })
  }

  handleJumpClick(event) {
    event.preventDefault()
    const destination = document.getElementById('jump-nav-1')
    smoothScroll(destination)
  }

  render() {
    return (
      <div>
        <Navigation />
        <header {...header.header} id="jump-nav-0">
          <p {...header.headerSubTitle}>- Jonathan Cooper -</p>
          <h1 {...header.headerTitle}>UI ENGINEER</h1>
          <button
            {...header.button}
            onClick={event => this.handleJumpClick(event)}
          >
            View Work
          </button>
        </header>
        <main {...content.wrap}>
          <TechWork />
          <DesignWork />
        </main>
        <Footer />
      </div>
    )
  }
}

IndexPage.PropTypes = {
  data: PropTypes.object,
}

export default IndexPage



